@import url("https://fonts.googleapis.com/css2?family=Charm:wght@700&family=Rochester&display=swap");

.home-search {
  max-width: 650px;
  top: 50%;
  left: 0;
  padding: 0 15px;
  transform: translateY(-50%);
  color: #fff;
}
.home-search p {
  margin: 2rem 0;
}
.home-search input {
  min-width: 400px;
}
.home-search button {
  color: #fff;
}
.home-search span {
  background-color: transparent;
  line-height: 24px;
  font-weight: 600;
  color: #fff;
  padding: 1px 12px 0;
  border: 1px solid #fff;
  border-radius: 40px;
  transition: all 0.2s ease-in-out;
  margin: 0 0.5rem;
}
.home-search span:hover {
  background-color: #fff;
  color: #404145;
  cursor: pointer;
}
.home-title {
  font-size: 48px;
  line-height: 56px;
}
.home-title i {
  font-family: "Rochester", cursive;
  font-size: 55px;
}
.home-partner {
  background-color: #fafafa;
  color: #b5b6ba;
}
.home-partner .container {
  padding: 1rem 0;
}
.home-partner p,
.home-partner img {
  font-weight: 500;
  padding: 0 2rem;
}
.home-page h2 {
  margin: 0;
  padding-bottom: 24px;
  font-size: 32px;
  line-height: 120%;
  font-weight: 700;
}
.home-page h5 {
  font-size: 1.2rem;
  font-weight: 600;
}
.home-slider-container {
  padding: 90px 0;
}

.home-introduce,
.home-explore {
  background-color: #f1fdf7;
  padding: 90px 0;
  font-size: 1.1rem;
}
.home-explore{
  padding: 90px 0 60px;
}
.home-introduce span {
  color: #404145;
}
.home-introduce-left {
  padding-left: 0;
  padding-right: 8%;
}
.home-introduce-right {
  position: relative;
}
.home-introduce-right span,
.slider-testimonial-img span {
  cursor: pointer;
  display: inline-block;
  background: url(https://fiverr-res.cloudinary.com/npm-assets/@fiverr/logged_out_homepage_perseus/apps/desktop-play-button.c1196d6.png)
    no-repeat;
  background-size: 80px;
  width: 80px;
  height: 80px;
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.home-introduce-right img {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.home-video-bg,
.testimonial-video-bg {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.65);
}
.home-video,
.testimonial-video {
  position: fixed;
  z-index: 5;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.home-video video {
  max-width: 900px;
}
.show-video {
  display: block;
}
.home-explore-item {
  position: relative;
  text-align: center;
  padding: 30px;
  width: 20%;
}
.home-explore-item img {
  display: block;
  width: 48px;
  height: 48px;
  margin: 0 auto 15px;
}
.home-explore-item a::after {
  content: "";
  position: absolute;
  top: 75px;
  right: 50%;
  transform: translateX(50%);
  padding: 0 0 8px;
  width: 48px;
  border-bottom: 2px solid #c5c6c9;
  transition: all 0.2s ease-in-out;
}
.home-explore-item a:hover::after {
  padding: 0 20px 8px;
  border-color: #1dbf73;
  width: 88px;
}
